<template>
  <el-container style="height: 100%; border: 1px solid #eee">
    <el-dialog
        title=""
        v-model="dialogFormVisible"
    >
      <!--:close-on-click-modal -->
      <!--:show-close x-->
      <el-form :model="gar_type">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="gar_type" placeholder="">
            <el-option v-for="(item,index) in GAR_TYPE" :key="index" :label="item"
                       :value="item"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <template #footer>
    <span class="dialog-footer">
      <el-button type="primary" @click="addGarbage"> </el-button>
    </span>
      </template>
    </el-dialog>
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
      <el-menu :default-openeds="['1']" @select="changeWindow">
        <el-submenu index="1">
          <template #title><i class="el-icon-user"></i></template>
          <!--            <template #title></template>-->
          <el-menu-item index="1-1"></el-menu-item>
          <el-menu-item index="1-2"></el-menu-item>
          <el-menu-item index="1-3"></el-menu-item>
        </el-submenu>
        <el-submenu index="2">
          <template #title><i class="el-icon-menu"></i></template>
          <el-menu-item index="2-1"></el-menu-item>
          <el-menu-item index="2-2"></el-menu-item>
        </el-submenu>
        <el-menu-item index="3" route><i class="el-icon-circle-close"></i></el-menu-item>
      </el-menu>
    </el-aside>

    <el-container>
      <el-header style="text-align: right; font-size: 12px">
        <el-button icon="el-icon-plus" style="margin-right: 15px" circle @click="dialogFormVisible=1"></el-button>
        <span>{{ username }}</span>
      </el-header>

      <el-main>
        <GetInfo v-if="this.choice===1"></GetInfo>
        <UpdateInfo v-if="this.choice===2"></UpdateInfo>
        <UpdatePassword v-if="this.choice===3"></UpdatePassword>
        <GetRecord v-if="this.choice===4"></GetRecord>
        <GetViolate v-if="this.choice===5"></GetViolate>
        <!--          <el-table-column prop="date" label="" width="140">-->
        <!--          </el-table-column>-->
        <!--          <el-table-column prop="name" label="" width="120">-->
        <!--          </el-table-column>-->
        <!--          <el-table-column prop="address" label="">-->
        <!--          </el-table-column>-->
        <!--        </el-table>-->
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import GetInfo from "@/components/GarbageMan/GetInfo";
import UpdateInfo from "@/components/GarbageMan/UpdateInfo"
import UpdatePassword from "@/components/GarbageMan/UpdatePassword";
import GetRecord from "@/components/GarbageMan/GetRecord";
import GetViolate from "@/components/GarbageMan/GetViolate";
import {Base64} from 'js-base64';

export default {
  components: {GetInfo, UpdateInfo, UpdatePassword, GetRecord, GetViolate},
  data() {
    return {
      choice: 1,
      formLabelWidth: '120px',
      username: Base64.decode(localStorage.getItem("username")),
      GAR_TYPE:[
        "",
        "",
        "",
        ""
      ],
      gar_type:"",
      dialogFormVisible:false
    }
  },
  methods: {
    changeWindow(index, indexPath) {
      console.log(1, indexPath)
      console.log(2, index);
      switch (index) {
        case("1-1"):
          this.choice = 1;
          break;
        case("1-2"):
          this.choice = 2;
          break;
        case("1-3"):
          this.choice = 3;
          break;
        case("2-1"):
          this.choice = 4;
          break;
        case("2-2"):
          this.choice = 5;
          break;
        case("3"):
          this.$router.push('/');
          break;
      }
    },
    addGarbage(){
      fetch(this.$store.state.URL + "/Garbage/Add?req=" + encodeURIComponent(this.gar_type), {
        method: "GET",
        headers: {
          "Authorization": "Bearer " + Base64.decode(localStorage.getItem("token")),
        },
      }).then((response) => {
        let result = response.json();
        result.then((result) => {
          console.log(result);
          this.dialogFormVisible=false;
        })
      })
    }
  }
};
</script>

