<template>
  <el-container style="height: 100%; border: 1px solid #eee">
    <!---->
    <el-dialog
        :close-on-click-modal='false'
        title=""
        :show-close='false'
        v-model="dialogFormVisible"
    >
    <!--:close-on-click-modal -->
    <!--:show-close x-->
      <el-form :model="Truck">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="Truck" placeholder="">
            <el-option v-for="(item,index) in TruckData" :key="index" :label="item"
                       :value="item"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <template #footer>
    <span class="dialog-footer">
      <el-button type="primary" @click="setTruck"> </el-button>
    </span>
      </template>
    </el-dialog>
    <!---->
    <el-dialog
        title=""
        v-model="addTransport"
    >
      <el-form :model="dustbin">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="dustbin" placeholder="">
            <el-option v-for="(item,index) in DustbinData" :key="index" :label="item.id"
                       :value="item.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <template #footer>
    <span class="dialog-footer">
      <el-button type="primary" @click="startTransport"> </el-button>
    </span>
      </template>
    </el-dialog>
    <!---->
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
      <el-menu :default-openeds="['1']" @select="changeWindow">
        <el-submenu index="1">
          <template #title><i class="el-icon-user"></i></template>
          <el-menu-item index="1-1"></el-menu-item>
          <el-menu-item index="1-2"></el-menu-item>
          <el-menu-item index="1-3"></el-menu-item>
        </el-submenu>
        <el-submenu index="2">
          <template #title><i class="el-icon-menu"></i></template>
          <el-menu-item index="2-1"></el-menu-item>
          <el-menu-item index="2-2">,</el-menu-item>
        </el-submenu>
        <el-menu-item index="3" route><i class="el-icon-circle-close"></i></el-menu-item>
      </el-menu>
    </el-aside>
    <!---->
    <el-container>
      <el-header style="text-align: right; font-size: 12px">
        <el-button icon="el-icon-plus" style="margin-right: 10px" circle @click="selectStart"></el-button>
        <el-button icon="el-icon-setting" style="margin-right: 15px" circle @click="dialogFormVisible=1"></el-button>
        <span>{{ username }}</span>
      </el-header>
      <!---->
      <el-main>
        <GetInfo v-if="this.choice===1"></GetInfo>
        <UpdateInfo v-if="this.choice===2"></UpdateInfo>
        <UpdatePassword v-if="this.choice===3"></UpdatePassword>
        <GetTransportRecord v-if="this.choice===4"></GetTransportRecord>
        <GetUnfinished v-if="this.choice===5"></GetUnfinished>
        <!--          <el-table-column prop="date" label="" width="140">-->
        <!--          </el-table-column>-->
        <!--          <el-table-column prop="name" label="" width="120">-->
        <!--          </el-table-column>-->
        <!--          <el-table-column prop="address" label="">-->
        <!--          </el-table-column>-->
        <!--        </el-table>-->
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import GetInfo from "@/components/Carrier/GetInfo";
import UpdateInfo from "@/components/Carrier/UpdateInfo"
import UpdatePassword from "@/components/Carrier/UpdatePassword";
import GetTransportRecord from "@/components/Carrier/GetTransportRecord";
//import GetUnfinished from "@/components/Carrier/getUnfinished";
import {Base64} from 'js-base64';

export default {
  components: {GetInfo, UpdateInfo, UpdatePassword, GetTransportRecord},
  data() {
    return {
      choice: 0,
      username: Base64.decode(localStorage.getItem("username")),
      Truck: String(),
      formLabelWidth: '120px',
      TruckData: [],
      DustbinData:[],
        dustbin:"",
      dialogFormVisible: false,// 
      addTransport:false// x
    }
  },
  methods: {
    changeWindow(index, indexPath) {
      console.log(1, indexPath)
      console.log(2, index);
      switch (index) {
        case("1-1"):
          this.choice = 1;
          break;
        case("1-2"):
          this.choice = 2;
          break;
        case("1-3"):
          this.choice = 3;
          break;
        case("2-1"):
          this.choice = 4;
          break;
        case("2-2"):
          this.choice = 5;
          break;
        case("3"):
          this.$router.push('/');
          break;
      }
    },
    setTruck() {
      if (this.Truck === '')
        return;
      localStorage.setItem("Truck", Base64.encode(this.Truck));
      fetch(this.$store.state.URL + "/Facility/Truck/SetTruck?req=" + this.Truck, {
        method: "GET",
        headers: {
          "Authorization": "Bearer " + Base64.decode(localStorage.getItem("token")),
        },
      }).then((response) => {
        let result = response.json();
        result.then((result) => {
          console.log(result)
        })
      })
      this.dialogFormVisible = false;
      this.choice = 1;
    },
    getFree() {
      fetch(this.$store.state.URL + "/Facility/Truck/GetFree", {
        method: "GET",
        headers: {
          "Authorization": "Bearer " + Base64.decode(localStorage.getItem("token")),
        },
      }).then((response) => {
        let result = response.json();
        result.then((result) => {
          console.log(result)
          this.TruckData = result;
        })
      })
    },
    async getTruck() {
      this.Truck = await (await fetch(this.$store.state.URL + "/Facility/Truck/GetTruck", {
        method: "GET",
        headers: {
          "Authorization": "Bearer " + Base64.decode(localStorage.getItem("token")),
        }
      })).text();
      if (this.Truck === "") {
        this.choice = 0;
        this.dialogFormVisible = 1;
      } else {
        this.choice = 1;
        this.dialogFormVisible = 0;
        localStorage.setItem("Truck", Base64.encode(this.Truck));
      }

    },
    async selectStart() {
      this.DustbinData = await (await fetch(this.$store.state.URL + "/Facility/TrashCan/GetAll", {
        method: "GET",
        headers: {
          "Authorization": "Bearer " + Base64.decode(localStorage.getItem("token")),
        }
      })).json();
      this.addTransport = true;
    },
    startTransport()
    {
      const req = {
        truck_id: Base64.decode(localStorage.getItem("Truck")),
        dustbin_id: this.dustbin
      };
      fetch(this.$store.state.URL + "/Transport/TransportStart", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "Authorization": "Bearer " + Base64.decode(localStorage.getItem("token")),
        },
        body:JSON.stringify(req)
      }).then((response) => {
        let result = response.json();
        result.then((result) => {
          console.log(result)
        })
      })
      this.addTransport = false;
    }
  },
  mounted() {
    //getTruck 
    this.getTruck();
    //
    this.getFree();
  },
};
</script>

<style>
html, body, #app, .el-container {
  /*0*/
  padding: 0px;
  /**/
  margin: 0px;
  /*100%*/
  height: 100%;
}

.el-header {
  background-color: #B3C0D1;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
</style>
