<template>
    <el-container style="height: 100%; border: 1px solid #eee">
      <el-dialog
          :close-on-click-modal='false'
          title=""
          :show-close='false'
          v-model="dialogFormVisible"
      >
        <el-form :model="workingStation">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="workingStation" placeholder="">
              <el-option v-for="(item,index) in stationData" :key="index" :label="item.name"
                         :value="item.name"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <template #footer>
    <span class="dialog-footer">
      <el-button type="primary" @click="setWorkingStation"> </el-button>
    </span>
        </template>
      </el-dialog>
      <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
        <el-menu :default-openeds="['1']" @select="changeWindow">
          <el-submenu index="1">
            <template #title><i class="el-icon-user"></i></template>
            <!--            <template #title></template>-->
            <el-menu-item index="1-1"></el-menu-item>
            <el-menu-item index="1-2"></el-menu-item>
            <el-menu-item index="1-3"></el-menu-item>
          </el-submenu>
          <el-submenu index="2">
            <template #title><i class="el-icon-menu"></i></template>
            <el-menu-item index="2-1"></el-menu-item>
            <el-menu-item index="2-2"></el-menu-item>
          </el-submenu>
          <el-menu-item index="3" route><i class="el-icon-circle-close"></i></el-menu-item>
        </el-menu>
      </el-aside>

      <el-container>
        <el-header style="text-align: right; font-size: 12px">
          <span>{{ username }}</span>
        </el-header>

        <el-main>
          <GetInfo v-if="this.choice===1" ></GetInfo>
          <UpdateInfo v-if="this.choice===2"></UpdateInfo>
          <UpdatePassword v-if="this.choice===3"></UpdatePassword>
          <GetRecord v-if="this.choice===4"></GetRecord>
          <GetViolate v-if="this.choice===5"></GetViolate>
          <!--          <el-table-column prop="date" label="" width="140">-->
          <!--          </el-table-column>-->
          <!--          <el-table-column prop="name" label="" width="120">-->
          <!--          </el-table-column>-->
          <!--          <el-table-column prop="address" label="">-->
          <!--          </el-table-column>-->
          <!--        </el-table>-->
        </el-main>
      </el-container>
    </el-container>
</template>

<script>
import GetInfo from "@/components/Watcher/GetInfo";
import UpdateInfo from "@/components/Watcher/UpdateInfo"
import UpdatePassword from "@/components/Watcher/UpdatePassword";
import GetRecord from "@/components/Watcher/getThrowRecord";
import GetViolate from "@/components/Watcher/getViolate";
import {Base64} from 'js-base64';

export default {
  components: {GetInfo, UpdateInfo, UpdatePassword, GetRecord, GetViolate},
  data() {
    return {
      choice: 0,
      username: Base64.decode(localStorage.getItem("username")),
      workingStation: String(),
      formLabelWidth: '120px',
      stationData: [],
      dialogFormVisible: true,
    }
  },
  methods: {
    changeWindow(index, indexPath) {
      console.log(1, indexPath)
      console.log(2, index);
      switch (index) {
        case("1-1"):
          this.choice = 1;
          break;
        case("1-2"):
          this.choice = 2;
          break;
        case("1-3"):
          this.choice = 3;
          break;
        case("2-1"):
          this.choice = 4;
          break;
        case("2-2"):
          this.choice = 5;
          break;
        case("3"):
          this.$router.push('/');
          break;
      }
    },
    setWorkingStation() {
      if(this.workingStation==='')
        return
      localStorage.setItem("workingStation", Base64.encode(this.workingStation));
      this.dialogFormVisible = false;
      this.choice=1;
    }
  },
  mounted() {
    if(localStorage.getItem("workingStation")===null)
    {
      this.choice=0;
      this.dialogFormVisible=1;
    }else{
      this.choice=1;
      this.dialogFormVisible=0;
    }
    fetch(this.$store.state.URL + "/Facility/Binsite/GetAll", {
      method: "GET",
      headers: {
        "Authorization": "Bearer " + Base64.decode(localStorage.getItem("token")),
      },
    }).then((response) => {
      let result = response.json();
      result.then((result) => {
        console.log(result)
        this.stationData = result;
      })
    })
  }
};
</script>

